import { createGlobalStyle } from 'styled-components';

import font from './font.woff';
import font2 from './font.woff2';
import font3 from './scrubby.otf'
import font4 from './Riccione-DemiBold.woff'
import font5 from './Riccione-DemiBold.woff2'
import font6 from './Riccione-Regular.otf'
export default createGlobalStyle`
    @font-face {
        font-family: 'Scrubby-Regular';
        src: local('Scrubby-Regular'), local('Scrubby-Regular'),
        url(${font2}) format('woff2'),
        url(${font}) format('woff');
        url(${font3}) format('otf');
        
        
    }
    @font-face {
        font-family: 'Riccione-DemiBold';
        src: local('Riccione-DemiBold'), local('Riccione-DemiBold'),
        url(${font5}) format('woff2'),
        url(${font4}) format('woff');

        
        
    }
    @font-face {
        font-family: 'Riccione-Regular';
        src: local('Riccione-Regular'), local('Riccione-Regular'),
        url(${font6}) format('otf');

        
        
    }
`;